import React from 'react';
import './About.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCode } from '@fortawesome/free-solid-svg-icons';
import { faGraduationCap } from '@fortawesome/free-solid-svg-icons';
import { faComments } from '@fortawesome/free-solid-svg-icons';

class About extends React.Component {

  render() {
    return (
      <div className="about" id="about">
        <div className="title"><span>About Me</span></div>
        <div className="content">
          <p><i><FontAwesomeIcon icon={faCode} /></i>Hello! I'm <span>João Rocha</span> and I am a developer.
          <br/>I wrote my first line of code in 2016 and since then I have been programming every day.
          <br/>My first ever completed project was an Android App made in Android Studio.
          <br/>Then I started learning Web Development to be a Fullstack Developer (front-end and back-end).
          <br/>
          <br/><i><FontAwesomeIcon icon={faGraduationCap} /></i>Currently studying Computer Science at FEUP.
          <br/>
          <br/><i><FontAwesomeIcon icon={faComments} /></i>Feel free to cantact me, I will respond as soon as I can.</p>
        </div>
      </div>
    );
  }
}

export default About;
