import React from 'react';
import './HomeHeader.css';
import profile from './img/profile.jpg';

class HomeHeader extends React.Component {

  render() {
    return (
      <div className="home-header">
        <div>
          <img src={profile} alt=""/>
          <span>João Rocha</span>
        </div>
      </div>
    );
  }
}

export default HomeHeader;
