import React from 'react';
import './Quote.css';

const quotes = [{
  quote: '"Whether you think you can or think you can’t, you’re right."',
  author: 'Henry Ford'
},{
  quote: '"There are no limits to what you can accomplish, except the limits you place on your own thinking."',
  author: 'Brian Tracy'
},
{
  quote: '"People who are crazy enough to think they can change the world, are the ones who do."',
  author: 'Rob Siltanen'
},
{
  quote: '"Do what you can with all you have."',
  author: 'Theodore Roosevelt'
},
{
  quote: '"The future belongs to those who believe in the beauty of their dreams."',
  author: 'Eleanor Roosevelt'
},
];

class Quote extends React.Component {

  constructor(props) {
    super(props);
    this.randomQuote = this.randomQuote.bind(this);

    const q = this.randomQuote();

    this.state = {
      quote: q.quote,
      author: q.author
    };
  }

  randomQuote() {
    const n = Math.floor(Math.random() * quotes.length);

    const q = {
      quote: quotes[n].quote,
      author: quotes[n].author
    };

    return q;
  }

  render() {
    return (
      <div className="quote" id="quote">
        <p>{this.state.quote}</p>
        <p>{this.state.author}</p>
      </div>
    );
  }
}

export default Quote;
