import React from 'react';
import './Socials.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import { faSteam } from '@fortawesome/free-brands-svg-icons'


class Socials extends React.Component {

  render() {
    return (
      <div className="socials">
        <div>
          <a href="https://twitter.com/J4yRx" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faTwitter} /></a>
          <span>Twitter</span>
        </div>
        <div>
          <a href="https://www.instagram.com/joao.s.rochinha/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faInstagram} /></a>
          <span>Instagram</span>
        </div>
        <div>
          <a href="https://steamcommunity.com/profiles/76561198242103140/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faSteam} /></a>
          <span>Steam</span>
        </div>
      </div>
    );
  }
}

export default Socials;
