import React from 'react';
import './Skills.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faJs } from '@fortawesome/free-brands-svg-icons'
import { faReact } from '@fortawesome/free-brands-svg-icons'
import { faHtml5 } from '@fortawesome/free-brands-svg-icons'
import { faCss3 } from '@fortawesome/free-brands-svg-icons'
import { faJava } from '@fortawesome/free-brands-svg-icons'
import { faDatabase } from '@fortawesome/free-solid-svg-icons'

class Skills extends React.Component {

  render() {
    return (
      <div className="skills">
        <div>
          <button><FontAwesomeIcon icon={faJs}/></button>
          <span>JavaScript</span>
        </div>
        <div>
          <button><FontAwesomeIcon icon={faReact}/></button>
          <span>React</span>
        </div>
        <div>
          <button><FontAwesomeIcon icon={faHtml5}/></button>
          <span>HTML</span>
        </div>
        <div>
          <button><FontAwesomeIcon icon={faCss3}/></button>
          <span>CSS</span>
        </div>
        <div>
          <button><FontAwesomeIcon icon={faJava}/></button>
          <span>Java</span>
        </div>
        <div>
          <button><FontAwesomeIcon icon={faDatabase}/></button>
          <span>SQL</span>
        </div>
      </div>
    );
  }
}

export default Skills;
